
.corpo {
    display: grid;
    min-height: 100vh;
    margin: 0px 100px 0px 100px;
    grid-template-columns: 300px 1fr;
    grid-template-rows: 100px 1fr 100px;
    grid-template-areas:
        'logo pesquisa'
        'conteudo conteudo'
        'paginacao paginacao';
  }
  
  .logo {
    grid-area: logo;

  }
  
  .pesquisa {
    grid-area: pesquisa;
  }
  
  .conteudo {
    padding-top: 80px;
    grid-area: conteudo;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
  }
  
  .paginacao {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-area: paginacao;
  }
  