
.personagens {
    display: flex;
}

.card {
    width: 280px;
    height: 280px;
    border-radius: 0px 0px 5px 5px;
    position: relative;
    margin: 0px 30px 30px 0px;
}   

.card div {
    width: 100%;   
    height: 100%;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
}

.card h2 {
   bottom: 15%; 
   font-size: 20px;
}

.card h5 {
    bottom: 5%; 
}

.card-conteudo {
   position: absolute; 
   margin-left: 10px;
   text-shadow: 3px 4px 2px #0c0c0c;
   text-transform: uppercase;
}

.card h4 {
    width:auto;
    height: 15%; 
    position: relative  ;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
}